********************/
/**
 * Base
 */
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
body {
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  line-height: 1.5;
  overflow-x: hidden;
  margin: 0;
  position: relative;
  width: 100%;
}
@media (min-width: 1200px) {
  body {
    font-size: 18px;
  }
}
a {
  color: blue;
  text-decoration: underline;
}
a:hover,
a:focus {
  color: #0000cc;
}
figure {
  margin: 0;
  background-size: auto 100%;
  background-position: center center;
  background-repeat: no-repeat;
}
figure img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  transition: opacity 0.2s;
}
figure.contain-width img {
  width: auto;
}
/* Elements
********************/
/*
 * Element: Buttons
 */
.button {
  cursor: pointer;
  background-image: none;
  background-color: #58a9e0;
  position: relative;
  font-weight: 600;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  font-size: 14px;
  transition: background 0.3s, color 0.3s, border-color 0.3s;
  color: #ffffff;
  border: 1px solid transparent;
  text-decoration: none;
  border-radius: 4px;
  height: 40px;
  padding: 0 20px;
}
.button:hover,
.button:focus {
  color: #ffffff;
  background: #2d93d8;
  outline: none;
}
.button[disabled],
[disabled] .button {
  cursor: not-allowed;
}
.button--ghost {
  color: #58a9e0;
  border: solid 1px #58a9e0;
  background-color: transparent;
}
.button--ghost:hover,
.button--ghost:focus {
  color: #ffffff;
  background-color: #58a9e0;
}
.button--large {
  height: 50px;
  padding: 0 40px;
  font-size: 24px;
}
/*
* Element: Headers
*/
.heading {
  color: #363636;
  font-size: 22px;
  font-weight: 600;
  font-family: 'SkipLegDay', 'Helvetica', 'Arial', 'sans-serif';
  margin-bottom: 0;
  margin-top: 0;
}
.heading__column {
  color: #000000;
}
.heading__hero {
  font-size: 30px;
  line-height: 34px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .heading__hero {
    font-size: 48px;
    line-height: 72px;
  }
}
.heading__footer {
  color: #ffffff;
  line-height: 40px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}
/*
* Element: Headers
*/
.heading-page {
  font-size: 24px;
  color: #363636;
  font-family: 'SkipLegDay', 'Helvetica', 'Arial', 'sans-serif';
}
/*
* Element: Input fields
*/
.input {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  vertical-align: bottom;
}
.input:focus {
  outline: 0;
}
/*
* Element: Input fields
*/
.input-inverted {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #ffffff;
  background-color: #000000;
  background-image: none;
  border: 1px solid #4b4b4b;
  border-radius: 10px;
}
.input-inverted:focus {
  outline: 0;
}
/*
* Element: Label
*/
.label {
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: bold;
}
.label--block {
  display: block;
}
/*
* Element: Label
*/
.label-inverted {
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #d7d7d7;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: bold;
}
.label-inverted--block {
  display: block;
}
/*
* Element: Label
*/
.textarea {
  display: block;
  width: 100%;
  min-height: 100px;
  padding: 10px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 10px;
  resize: none;
}
.textarea:focus {
  outline: none;
}
/*
* Element: Label
*/
.textarea-inverted {
  display: block;
  width: 100%;
  min-height: 100px;
  padding: 10px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #ffffff;
  background-color: #000000;
  background-image: none;
  border: 1px solid #4b4b4b;
  border-radius: 10px;
  resize: none;
}
.textarea-inverted:focus {
  outline: none;
}
/*
 * Element: Image
 */
.image {
  width: 100%;
}
/*
 * Element: Link
 */
.link {
  color: #58a9e0;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
}
.link:hover,
.link:focus {
  color: #2d93d8;
  outline: none;
}
/*
 * Element: Link Buttons
 */
.link-button {
  display: inline-block;
  cursor: pointer;
  background-image: none;
  background-color: #58a9e0;
  font-weight: 600;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  font-size: 14px;
  transition: background 0.3s, color 0.3s, border-color 0.3s;
  color: #ffffff;
  border: 1px solid transparent;
  text-decoration: none;
  border-radius: 4px;
  line-height: 40px;
  padding: 0 20px;
}
.link-button:hover,
.link-button:focus {
  color: #ffffff;
  background: #2d93d8;
  text-decoration: none;
  outline: none;
}
.link-button[disabled],
[disabled] .link-button {
  cursor: not-allowed;
}
.link-button--ghost {
  color: #363636;
  border: solid 1px #58a9e0;
  background-color: transparent;
}
.link-button--ghost:hover,
.link-button--ghost:focus {
  color: #ffffff;
  background-color: #58a9e0;
}
/*
 * Element: Link Social
 */
.social-link {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #acacac;
  border-radius: 50%;
}
.social-link:hover,
.social-link:focus {
  background: #939393;
}
.social-link__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  fill: #ffffff;
}
.social-link--facebook {
  background-color: #3b5998;
}
.social-link--facebook:hover,
.social-link--facebook:focus {
  background: #2d4373;
}
.social-link--twitter {
  background-color: #00aced;
}
.social-link--twitter:hover,
.social-link--twitter:focus {
  background: #0087ba;
}
.social-link--google-plus {
  background-color: #d34836;
}
.social-link--google-plus:hover,
.social-link--google-plus:focus {
  background: #b03626;
}
/*
 * Element: Logo
 */
.logo {
  width: 150px;
}
/*
* Element: paragraphs
*/
.paragraph--hero {
  color: #ffffff;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .paragraph--hero {
    font-size: 21px;
  }
}
/* Modules
********************/
/*
 * Module: Box
 */
.box__inner {
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  background-color: #ffffff;
}
.box__image {
  width: 100%;
}
.box__content {
  padding: 20px;
}
.box p + p {
  margin-top: 15px;
}
/*
 * Module: Box Image
 */
.box-image {
  position: relative;
}
.box-image__inner {
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 300px;
  position: relative;
}
.box-image__content {
  background-color: rgba(255, 255, 255, 0.75);
  bottom: 0;
  left: 0;
  padding: 20px;
  position: absolute;
  right: 0;
}
.box-image p + p {
  margin-top: 15px;
}
/*
 * Module: Text Box
 */
.text-box p + p,
.text-box--centered p + p {
  margin-top: 15px;
}
.text-box--centered {
  text-align: center;
}
.text-box--centered__inner {
  padding: 20px;
}
.form-field + .form-field {
  margin-top: 20px;
}
.form-field + .form-field {
  margin-top: 20px;
}
.link-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.link-list__item + .link-list__item {
  margin-top: 5px;
}
/*
 * Module: Nav Main List
 */
@media (max-width: 992px) {
  .nav-main {
    display: none;
  }
}
.nav-main-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.nav-main-list__item {
  display: inline-block;
  margin-right: 50px;
}
.nav-main-list__item a {
  color: #ffffff;
  text-decoration: none;
}
.nav-main-list__item:hover {
  color: #ffffff;
}
.nav-main-list__item:hover a {
  color: #ffffff;
  text-decoration: underline;
}
.nav-main-list__item:last-child {
  margin-right: 0;
}
a.nav-main-list__item--active {
  color: #58a9e0;
  text-decoration: underline;
}
/*
 * Modul: Social Media Collection
 */
.social-media__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 20px;
}
.social-media__item {
  float: left;
}
.social-media__item + .social-media__item {
  margin-left: 10px;
}
/*
 * Module: Topbar
 */
.topbar {
  background-color: #121b20;
}
.topbar__inner {
  align-items: center;
  display: flex;
}
.topbar__logo {
  flex-basis: 30%;
  padding-bottom: 20px;
  padding-top: 20px;
}
.topbar__navigation {
  align-content: flex-end;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  flex-basis: 70%;
  padding-bottom: 20px;
  padding-top: 20px;
}
.topbar__navigation-primary {
  align-items: center;
  display: flex;
  padding-right: 20px;
  padding-top: 20px;
}
.topbar__navigation-secondary {
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  color: #ffffff;
  padding-bottom: 20px;
  padding-right: 20px;
  text-align: right;
}
.topbar__navigation-search {
  margin-left: auto;
  width: 300px;
}
/* Components
********************/
/*
 * Component: Footer
 */
.footer {
  background-color: #121b20;
  color: #ffffff;
  padding-top: 30px;
  padding-bottom: 30px;
}
/**
 * Component: Hero
 */
.hero {
  color: #ffffff;
  padding-bottom: 60px;
  padding-top: 60px;
  position: relative;
  background-size: cover;
}
@media (min-width: 992px) {
  .hero {
    height: 500px;
    padding: 230px 0;
  }
}
@media (min-width: 1200px) {
  .hero {
    height: 700px;
  }
}
.hero:before {
  background-color: rgba(0, 0, 0, 0.3);
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
}
.hero__content {
  position: relative;
}
@media (min-width: 992px) {
  .hero__content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
}
.hero__info {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .hero__info {
    font-size: 21px;
  }
}
/*
* Module: image text section
*/
.image-text-section {
  padding: 40px;
}
/*
* Module: image text section
*/
.text-image-section {
  padding: 40px;
}
/* Helpers
********************/
/*
 * Helpers: Main
 */
.main {
  margin-bottom: 50px;
  margin-top: 50px;
}
/*
 * Helpers: Grid
 */
.grid {
  display: flex;
  justify-content: center;
  padding-left: 30px;
  padding-right: 30px;
}
.grid + .grid {
  margin-top: 30px;
}
.grid__inner {
  display: flex;
  flex-wrap: wrap;
  flex-basis: 100%;
  margin-right: -calc(15px);
  margin-left: -calc(15px);
}
.grid--full-width {
  max-width: 2000px;
  padding-left: 0;
  padding-right: 0;
}
.grid--site-width {
  max-width: 1560px;
}
.grid--two-columns > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-columns > .grid-column {
    width: calc(50% - 30px);
  }
}
.grid--three-columns > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--three-columns > .grid-column {
    width: calc(33.33% - 30px);
  }
}
.grid--two-third > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-third > .grid-column {
    width: calc(66.66% - 30px);
  }
}
.grid--two-third > .grid-column:last-child {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-third > .grid-column:last-child {
    width: calc(33.33% - 30px);
  }
}
.grid-column {
  flex-grow: 1;
  margin-right: calc(15px);
  margin-left: calc(15px);
  margin-bottom: 30px;
}
@media (min-width: 992px) {
  .grid-column {
    margin-bottom: 0;
  }
}
